<template>
  <form class="p-2" @submit.prevent="uploadDocument">
    <div class="w-full text-center font-medium text-xl pb-5 uppercase font-semibold text-gray-800 mb-4">
         
    </div>
    <DoubleGridWrapper>
      <template #first>
        <LineInput text="" v-model:enterElement="docData.seria" :require="true"/>
      </template>
      <template #second>
        <LineInput text="" v-model:enterElement="docData.number" :require="true"/>
      </template>
    </DoubleGridWrapper>
    <DoubleGridWrapper>
      <template #first>
        <LineInput text=" " v-model:enter-element="docData.name"/>
      </template>
      <template #second>
        <div class="relative z-auto w-full flex-col">
          <label for="type"
                 class="block mb-2 text-sm  text-gray-500 dark:text-gray-400">
             :</label>
          <select id="type"
                  v-model="docData.type"
                  class="bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-primary-500 focus:border-primary-500 block w-full p-2.5 dark:bg-gray-700 dark:border-gray-600 dark:placeholder-gray-400 h-2/3 dark:text-white dark:focus:ring-blue-500 dark:focus:border-blue-500">
            <option value="1">  </option>
            <option value="2"> </option>
            <option value="4">  </option>
            <option value="5">  </option>
          </select>
        </div>
      </template>
    </DoubleGridWrapper>
    <div>
      <DatepickerAdapter v-model:enterElement="docData.date" text=" "/>
    </div>

    <DocDownloadField text="  :" @uploadFile="(n)=> file = n " @deleteDocument="file=null">
        -   15 ;
        <br>
        - : JPG, PDF
    </DocDownloadField>
    <hr class="my-4"/>
    <div class="flex justify-center ">
      <button
          class="text-white  bg-primary-700  font-medium rounded-lg text-sm w-full sm:w-auto px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 dark:focus:ring-primary-800">
        <Spinner v-if="uploadFlag" color="green" size="5" class="mx-2.5"/>
        <div v-else></div>
      </button>
    </div>

  </form>
</template>

<script setup lang="ts">
import DoubleGridWrapper from "../UtilityIrgupsComponents/DoubleGridWrapper.vue";
import LineInput from "../UtilityIrgupsComponents/LineInput.vue";
import {ref} from "vue";
import {ParentDocument} from "../../DTO";
import DatepickerAdapter from "../DatepickerAdapter.vue";
import DocDownloadField from "../UtilityIrgupsComponents/DocDownloadField.vue";
import {useUserStore} from "../../stores/useUserStore";
import {getPersonId, uploadFile} from "../../services";
import {Spinner} from "flowbite-vue";

const uploadFlag = ref<boolean>(false)
const docData = ref<ParentDocument>({} as ParentDocument)
const file = ref<File | null>()
const emits = defineEmits(['close', 'upload'])
const uploadDocument = async () => {
  uploadFlag.value = true
  docData.value.person_id = await getPersonId(useUserStore().user) as number
  if (file.value !== undefined && file.value !== null) {
    await uploadFile(useUserStore().user, file.value, docData.value)
  }
  emits('close')
  emits('upload')
  uploadFlag.value = false
}
</script>

