import {User, UserLogin, UserRegister} from "../DTO";
import {instance} from "./instance";



export const authenticate = async(user: UserLogin): Promise<User> => {
    return await instance.post(`/api/login `, {
        email: user.email,
        password: user.password,
      }).then((response) => 
        response.data
      )
}
export const register = async (user: UserRegister) => {
    return await instance.post(`/api/register`, user).then((response)=> response.data.user).catch((error)=>{
        console.log(error.response)
    })
}

export const show = async (user: User) =>
{
    return await instance.post(`/api/login `, user).catch((error) => {
        if (error.response.status === 404 ) {
            return "Not Found";
        }
        if (error.response.status === 401){
            return "Unauthorized";
        }
    })
}
export const confirm = async (userID:Number, code:String) =>{
    let content = {user: userID, code: code}
    return await instance.post(`/api/user/confirm`,content).catch((error)=>{
        console.log(error.response)
    })
}