import {ParentDocument, User} from "../DTO";
import {getPersonId} from "./person";
import {instance} from "./instance";

export const uploadFile = async (user: User, file: File, dataFile: ParentDocument) => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    let content = {
        file: file,
        ...dataFile
    }
    return await instance.post(`/api/parent/upload`, content).then((response) => {
        console.log(response)
        response.status
    })
}
export const getDocuments = async (user: User, id?: string) => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    const personId = id ? id : await getPersonId(user);
    return await instance.get(`/api/parent/${personId}`).then((response) => response.data)
        .catch((error) => {
            if (error.response.status === 401) {
                return "Unauthorized";
            } else return "Not Found"
        });
}
export const delDocuments = async (user: User, id: number) =>{
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    return await instance.delete(`/api/parent/${id}`).then()
        .catch((error) => {
            if (error.response.status === 401) {
                return "Unauthorized";
            } else return "Not Found"
        });
}