import {Person, User} from "../DTO";
import {instance} from "./instance";


export const getPersonId = async (user: User): Promise<number|string> => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    return await instance
        .get(`/api/user/${user.id}/person`)
        .then((response) => response.data.data)
        .catch((error) => {
            if (error.response.status === 404) {
                return "Not Found";
            }
            if (error.response.status === 401) {
                return "Unauthorized";
            }
        })
};

export const getPerson = async (user: User, id?: string): Promise<Person | String> => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    const personId = id ? id : await getPersonId(user);
    return await instance.get(`/api/person/${personId}`).then((response) => response.data)
        .catch((error) => {
            if (error.response.status === 401) {
                return "Unauthorized";
            } else return "Not Found"
        });
};

export const updatePerson = async (
    user: User,
    person: Person
): Promise<number> => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    return await instance.patch(`/api/person/${person.id}`, person).then((response)=> response.status);
};
