import {Mark, User} from "../DTO";
import {instance} from "./instance";


export const getMarks = async (user: User, id: number): Promise<Array<Mark>> => {
    instance.defaults.headers.common['Authorization'] = `Bearer ${user.token}`
    return await instance.get(`/api/student/${id}/marks`)
        .then((response) => {
            return response.data})
        .catch((error) => {
            if (error.status === 404) {
                return "Not Found";
            }
            if (error.status === 401) {
                return "Unauthorized";
            }
        });
}