import {useUserStore} from "../stores/useUserStore";
import AuthPage from "../views/AuthPage.vue";
import {createRouter, createWebHistory} from "vue-router";
import PersonPage from "../views/PersonPage.vue";
import NotFound from "../views/NotFound.vue";
import FamilyPage from "../views/FamilyPage.vue";
import ChildPage from "../views/ChildPage.vue";
import OrdersPage from "../views/OrdersPage.vue";
import AttendancePage from "../views/AttendancePage.vue";
import SchedulePage from "../views/SchedulePage.vue";
import AcademicPlanPage from "../views/AcademicPlanPage.vue"
import AcademicPerformance from "../views/AcademicPerformancePage.vue"
import DebtPage from  "../views/DebtPage.vue"

const subUrl = import.meta.env.VITE_BACKEND_SUB_URL;
const router = createRouter({
    history: createWebHistory(import.meta.env.BASE_URL),
    routes: [
        {path: `/${subUrl}/login`, name: "AuthPage", component: AuthPage,},
        {path: `/${subUrl}/parent`, name: "ParentPage", component: PersonPage,},
        {path: `/${subUrl}/:pathMatch(.*)*`, name: "NotFound", component: NotFound},
        {path: `/${subUrl}/family`, name: "FamilyPage", component: FamilyPage},
        {path: `/${subUrl}/family/:id`, name: 'Child', component: ChildPage},
        {path: `/${subUrl}/family/:id/orders`, name: 'Orders', component: OrdersPage},
        {path: `/${subUrl}/family/:id/attendance`, name: 'Attendance', component: AttendancePage},
        {path: `/${subUrl}/family/:id/schedule`, name: 'Schedule', component: SchedulePage},
        {path: `/${subUrl}/family/:id/uchplan`, name: 'Uchplan', component: AcademicPlanPage},
        {path: `/${subUrl}/family/:id/academicPerformance`, name: 'AcademicPerformance', component: AcademicPerformance},
        {path: `/${subUrl}/family/:id/debt`, name: 'Debt', component: DebtPage}
    ],
});
router.beforeEach(async (to, from) => {
    const userStore = useUserStore();
    if (
        // make sure the user is authenticated
        !userStore.token &&
        //  Avoid an infinite redirect
        to.name !== "AuthPage"
    ) {
        // redirect the user to the login page
        return {name: "AuthPage"};
    }
});

export default router;
